@use "sass:math";

$clock-size: 250px;
$tick-step-size: 20px;
$pin-size: 15px;

.clock {
    padding: 0px;
    margin: 0px;
    background-color: #cccccc;
    width: 300px;
    // height: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-style: dotted;
    border-color: #999999;
    border-width: 1px;
    border-radius: 5px;
}

.analog-clock {
    padding: 0px;
    margin: 0px;
    display: flex;
    position: relative;
    width: $clock-size;
    height: $clock-size;
    background-color: #ffffff;
    border-style: solid;
    border-width: 1px;
    border-color: #00000000;
    border-radius: 50%;
    background-image: url("../assets/dial.png");
    background-repeat: no-repeat;
    background-size: contain;
}

.numbers {
    display: none !important;
    position: relative;
    padding: 0px;
    margin: 0px;
    width: $clock-size;
    height: $clock-size;
    display: flex;
    font-weight: bold;
    font-size: large;
}

.hour-12 {
    padding: 0px;
    margin: 0px;
    position: absolute;
    width: $clock-size;
    height: $clock-size;
    justify-content: center;
    display: flex;
}

.hour-6 {
    padding: 0px;
    margin: 0px;
    position: absolute;
    width: $clock-size;
    height: $clock-size;
    justify-content: center;
    align-items: flex-end;
    display: flex;
}

.hour-3 {
    padding: 0px;
    margin: 0px;
    position: absolute;
    width: $clock-size;
    height: $clock-size;
    justify-content: flex-end;
    align-items: center;
    display: flex;
}

.hour-9 {
    padding: 0px;
    margin: 0px;
    position: absolute;
    width: $clock-size;
    height: $clock-size;
    justify-content: flex-start;
    align-items: center;
    display: flex;
}

.digital-clock {
    margin: 10px;
}

.dial {
    position: absolute;
    width: $clock-size;
    height: $clock-size;
}

.seconds-svg {
    width: 3px;
    height: $clock-size;
    transform-origin: bottom;
}

.seconds-tick {
    width: 3px;
    height: math.div($clock-size, 2);
    fill: rgb(0, 255, 0);
    transform-origin: bottom;
    z-index: 2;
}

.seconds {
    display: flex;
    padding: 0px;
    margin: 0px;
    position: absolute;
    width: $clock-size;
    height: $clock-size;
    justify-content: center;
    align-items: flex-start;
}

.minutes-svg {
    margin-top: $tick-step-size;
    width: 3px;
    height: math.div($clock-size, 2) - $tick-step-size;
    transform-origin: bottom;
}

.minutes-tick {
    margin-top: $tick-step-size;
    width: 3px;
    height: math.div($clock-size, 2) - $tick-step-size;
    fill: rgb(25, 0, 255);
    transform-origin: bottom;
    z-index: 3;
}

.minutes {
    display: flex;
    padding: 0px;
    margin: 0px;
    position: absolute;
    width: $clock-size;
    height: $clock-size;
    justify-content: center;
    align-items: flex-start;
}

.hours-svg {
    margin-top: ($tick-step-size * 2.5);
    width: 5px;
    height: math.div($clock-size, 2) - ($tick-step-size * 2.5);
    transform-origin: bottom;
}

.hours-tick {
    margin-top: ($tick-step-size * 2.5);
    width: 5px;
    height: math.div($clock-size, 2) - ($tick-step-size * 2.5);
    fill: rgb(255, 0, 0);
    transform-origin: bottom;
    z-index: 4;
}

.hours {
    display: flex;
    padding: 0px;
    margin: 0px;
    position: absolute;
    width: $clock-size;
    height: $clock-size;
    justify-content: center;
    align-items: flex-start;
}

.pin-cover {
    position: absolute;
    width: 250px;
    height: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
}

#pin {
    width: $pin-size;
    height: $pin-size;
    padding: 0px;
    margin: 0px;
    background-color: #000000;
    border-radius: 50%;
    z-index: 5;
}

.zone-info {
    font-weight: bold;
    font-size: large;
    margin-top: 10px;
}

.int-clock-container {
    display: flex;
    flex-direction: column;
}

.country-list {
    margin-top: 10px;
    margin-bottom: 10px;
}

.country-list-label {
    margin: 10px;
    margin-left: 0px;
    font-weight: bold;
}
